.gallery {
    margin-left: auto;
    margin-right: auto;
}

.gallery__container {
    margin: auto;

    // Tablet styles
    @media screen and (min-width: 768px) {
        padding-left: 32px;
        padding-right: 32px;
    }

    // Desktop styles
    @media screen and (min-width: 1200px) {
        padding-left: 94px;
        padding-right: 94px;
    }
}

.gallery__image {
    margin-left: auto;
    margin-right: auto;
    max-width: 100%;

    border-radius: 8px;

    // Tablet styles
    @media screen and (min-width: 768px) {
        border-radius: 18px;
    }

    // Desktop styles
    @media screen and (min-width: 1200px) {
        border-radius: 24px;
    }
}